                            <td>{{config.yeshixitong}}</td>
                        </tr>
                         <tr>
                            <td>中控液晶屏分屏显示</td>
                            <td>{{config.zhongkongyejing}}</td>
                        </tr>
                         <tr>
                            <td>自适应巡航</td>
                            <td>{{config.zishiyingxunhang}}</td>
                        </tr>
                         <tr>
                            <td>全景摄像头</td>
                            <td>{{config.quanjingshexiangtou}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="modal-footer"></div>
            </div><!-- /.modal-content -->
        </div><!-- /.modal-dialog -->
    </div><!-- /.modal -->
</template>

<script>
export default {
    props:{
        config:Object
    }
}
</script>



